import url_base from '@/utils/base'

const {
  BASE_URL,
  information
} = url_base

const api = {
  BASE_URL,  

  // exam
  getNotes: `${BASE_URL}/exam/notes/getNotes`, //


  // textbook
  getTextBookList:  `${BASE_URL}/textbook/getList`, //



  
  detailPolicy: (type)=>{
     if (type == 'owner') return `${BASE_URL}/owner-policy-detail/`
     return `${BASE_URL}/wenzheng/detail/`
  } , //


  /** */
  fetchUserInfo: `${BASE_URL}/user/my`,
  refreshToken: `${BASE_URL}/user/token/refresh`,
  fetchLevel: `${BASE_URL}/user/my-level`, // 


  /** */
  uploadFile: `${information}/image/upload`, //  post






}

export default api



