import {
  fetchClientFunction
} from "@/utils/interface";
import {
  Toast
} from 'vant'
import {
  setStore,
  getStore,
  clearStore
} from '@/utils/storage'

// 
export const loadLogin = () => {
  clearStore("accessToken")
  Toast.fail('')
  const data = {
    type: "login",
    params: '',
  };
  fetchClientFunction(data);
}
// token 
export const loadToken = () => {
  let data = {
    type: "token",
    params: "",
  };
  fetchClientFunction(data);
  console.log("fetchtoken end");
}
// token 
export const checkToken = () => {
  // loadToken()
  // console.log(getStore('accessToken'),"checkToken")
  // if (!getStore('accessToken')) {
  //   loadLogin()
  //   return false
  // }
  return true
}



